export interface IUpdatable {
    update(diff: number): void;
}

export interface IRenderable {
    render(): void;
}

export interface IPosition {
    /** left */
    x: number;
    /** top */
    y: number;
}

export interface IScene<T extends ISceneObject> extends IUpdatable, IRenderable {
    prepare(): void;
    clear(): void;
    add(sceneObject: T): void;
    remove(sceneObject: T): void;
}

export interface ISceneObject extends IUpdatable, IRenderable {
    readonly element: HTMLElement;
    position: IPosition
}

export interface IAIControlled {
    addAI(ai: IUpdatable): void;
}

export interface IHittable {
    hit(damage: number): void;
}